/* sort bar */
.sort-bar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  margin: 0px auto 10px auto;
}

.sort-bar > * {
  font-family: GOTHAM-BLACK;
  color: var(--color-lighter-gray);
  padding: 8px 15px;
  border-bottom: 1px solid var(--color-lighter-gray);
}

.sort-bar > *:hover {
  color: var(--color-black);
}

.sort-chosen {
  color: var(--color-black);
  border-bottom: 2px solid var(--color-black);
}

.sort-bar-leftover {
  width: 100%;
}

/* feed games */
.feed-games {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 20px;
}

.feed-games > * {
  font-size: .7em;
  padding: 10px;
  margin: 0px 5px 5px 0px;
  color: var(--color-black);
  background: var(--color-lightest-gray);
  white-space: nowrap;
}

.feed-games > *:hover {
  background: var(--color-lighter-gray);
  cursor: pointer;
}



