/* video holder */
.main-video-holder {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  margin-bottom: 30px;
}

.main-video-title {
  font-family: GOTHAM-BLACK;
  margin-bottom: 5px;
}

.main-video-description {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  margin: 0px 0 8px 0;
}

.main-video-userimg {
  width: 40px;
  height: 40px;
  background: var(--color-light-orange);
  margin-right: 10px;
}

.main-video {
  height: var(--main-video-height);
  width: calc(var(--main-video-height)*1/0.56);
}

